.card{
    display: flex;
    flex-direction: column;
    background-color: aquamarine;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 20px;
    cursor: pointer;
    backface-visibility: hidden;
    transition: transform 0.6s ease-out;
}

.card:hover{
    transform: scale(1.05);
}